.search {
    background-color: lightgray;
    padding: 20px;

    .search-container {
        position: relative;
        width: 90%;
        margin: 0 auto;

    }

    input {
        background-color: white;
        width: 100%;
        height: 25px;
        border-radius: 5px;
        padding-left: 25px;

        &::placeholder {
            color: lightgray;
        }


    }


    .icon {
        position: absolute;
        left: 5px;
        top: 50%;
        transform: translateY(-50%);
        width: 15px;
        height: 15px;

    }
}