@import '../../scss/mixin.scss';

.movie-modal {
    @include abcCenter;
    z-index: 5;
    height: 700px;
    width: 80%;
    background-color: white;
    padding: 25px;
    position: fixed;

    .details {
        display: flex;
        flex-direction: column;
        // align-items: center;

        img {
            margin: 0 auto;
            width: 100%;
            max-width: 350px;
        }

        .detail {
            margin-top: 8px;
        }
    }

    .close {
        position: absolute;
        right:3px;
        top: 3px;
        cursor: pointer;

    }
}